"use client"
import Link from 'next/link';
import { usePathname } from 'next/navigation';
function Navbar() {
    const pathname = usePathname();
    const navLinks: { id: number, name: string, href: string }[] = [
        {
            id: 1,
            name: "Home",
            href: "/"
        },
        {
            id: 2,
            name: "Count",
            href: "/count"
        },
        {
            id: 3,
            name: "Login",
            href: "/login"
        },
        {
            id: 3,
            name: "Services",
            href: "/services"
        }
    ];
    return (
        <div>
            <nav className='flex justify-between p-4'>
                {navLinks.map(nav => {
                    const isActive = pathname === nav.href || (pathname.startsWith(nav.href) && nav.href !== "/")
                    return (
                        <Link className={isActive ? "text-blue-500 font-bold" : "text-gray-700 font-bold"} key={nav.id} href={nav.href}>{nav.name}</Link>
                    )
                })}
            </nav>
        </div>
    )
}

export default Navbar
